.sg-colors {
    display:-webkit-box;
    display:-webkit-flex;
    display:-ms-flexbox;
    display:flex;
    -webkit-flex-wrap:wrap;
        -ms-flex-wrap:wrap;
            flex-wrap:wrap;
}
.sg-colors li {
    -webkit-box-flex:0;
    -webkit-flex:0 0 auto;
        -ms-flex:0 0 auto;
            flex:0 0 auto;
    width:calc(99.99% * 1/4 - (30px - 30px * 1/4));
    border-radius:5px;
    overflow:hidden;
    box-shadow: 0 3px 0px $light_gray;
    clear:both;
    *zoom:1;
    margin-bottom:30px;
}
.sg-colors li:nth-child(1n) {
    margin-right:30px;
}
.sg-colors li:last-child {
    margin-right:0;
}
.sg-colors li:nth-child(4n) {
    float:right;
}
.sg-colors li:nth-child(4n) {
    margin-right:0;
}
.sg-colors li:before {
    content:'';
    display:table;
}
.sg-colors li:after {
    content:'';
    display:table;
    clear:both;
}
.sg-colors li:before, .sg-colors li:after {
    content:" ";
    clear:both;
    display:block;
}
.sg-colors .sg-swatch {
    float:left;
    display:block;
    width:50%;
    height:75px;
}
.sg-colors .sg-label {
    clear:both;
    display:block;
    float:none;
    font-weight:bold;
}
.sg-colors .text {
    clear:both;
    *zoom:1;
    padding:10px;
    clear:both;
}
.sg-colors .text:before {
    content:'';
    display:table;
}
.sg-colors .text:after {
    content:'';
    display:table;
    clear:both;
}
.sg-colors .text:before, .sg-colors .text:after {
    content:" ";
    clear:both;
    display:block;
}